.app {
  text-align: center;
}

/* override Button */
.btn:focus {
  outline: none !important;
}

/* override Col */
.col-xs-4 {
  padding: 5px;
}

/* override Panel */

.panel {
  margin: 0 5px 5px;
}

.panel:first-child {
  margin-top: 5px;
}

.panel-heading {
  align-items: center;
  display: flex;
  justify-content: space-between;
}

.panel-body {
  display: flex;
  flex-wrap: wrap;
}

.panel-body button {
  flex: 1;
}

/* override Modal */

.modal {
  display: flex !important;
}

.modal-dialog {
  display: flex;
  flex: 1;
  flex-direction: column;
  justify-content: center;
  margin-left: 30px;
  margin-right: 30px;
  overflow: auto;
}

.modal-content {
  display: flex;
  flex-direction: column;
}

.modal-body {
  overflow-x: hidden;
  overflow-y: scroll;
  text-align: center;
}

.modal-footer {
  text-align: center;
}

/* Tunnel */

.tunnel {
  border: 3px solid transparent;
}

.tunnel.selected {
  border-color: lightsalmon;
}

.tunnel .panel-heading {
  background-size: cover;
  background-position: center;
}

.tunnel .label {
  background-color: rgba(0,0,0,0.5);
  font-size: 18px;
  overflow: auto;
  padding: .2em .2em .3em;
  text-overflow: ellipsis;
}

.memo {
  margin-top: 1px;
  border: 1px solid black;
}

/* Nameko */

.all-block {
  margin-left: -5px;
  margin-right: -5px;
}

.nameko {
  padding: 2px;
  height: 150px;
}
@media(min-width:768px) {
  .nameko {
    height: 230px;
  }
}

.nameko button {
  padding: 3px 6px;
  width: 100%;
}
.nameko.selected button {
  border-color: lightblue;
  border-width: 3px;
  padding: 1px 4px;
}

.nameko .img-ctn {
  height: 80px
}

.nameko .img {
  margin-bottom: -14px;
  max-height: 80px;
  max-width: 100%;
}

.skill-ctn {
  display: flex;
  height: 18px;
}
@media(min-width:768px) {
  .skill-ctn {
    height: 36px;
  }
}

.skill {
  background-color: transparent;
  background-position: center;
  background-size: cover;
  border: none;
  filter: grayscale(100%);
  height: 18px;
  margin: 0 2px;
  width: 18px;
}
@media(min-width:768px) {
  .skill {
    height: 36px;
    width: 36px;
  }
}

.skill.selected {
  filter: grayscale(0%);
}

.value-area {
  display: flex;
  justify-content: center;
}

.value-area > div {
  display: flex;
  flex-direction: column;
  margin: 0 5px;
}

.value-area .btn {
  background-color: lightgray;
  border-width: 0;
  border-radius: 50%;
  height: 44px;
  padding: 0;
  width: 44px;
}

/* Run */

.run-placeholder {
  height: 66px;
}

.run {
  position: fixed;
  bottom: 20px;
  transform: translate(-50%,0);
}

.run span {
  margin-right: 6px;
}
